import React, { useEffect, useMemo } from 'react'
// import AniLink from "gatsby-plugin-transition-link/AniLink"
import { getImage } from 'gatsby-plugin-image';
import { useWindowSize } from 'react-use';
import { useToken } from '@chakra-ui/react';
import { useFormikContext } from 'formik';

import Box from '../../components/Box'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Container from '../../components/Container'
// import SwipeLink from '../../components/SwipeLink'
import PaintLink from '../../components/PaintLink'

import Layout from '../Layout';
import { responsive } from '../../components/ThemeProvider/theme'
import Banner from './Banner';

const hanldeImages = layer => ({
  ...layer,
  image: getImage(layer.image),
})

const IndexPage = ({ data: { allMobileJson } }) => {
  const { resetForm } = useFormikContext()
  const [teal] = useToken('colors', ['custom.teal'])
  const mobileImages = useMemo(() => allMobileJson.nodes.map(hanldeImages).reverse(), [allMobileJson])
  const { height } = useWindowSize()
  useEffect(() => {
    setTimeout(resetForm)
  }, [resetForm])
  return (
    <Layout
      footerProps={{
        bg: responsive('custom.lightBlueBg', height > 630 ? 'transparent' : 'custom.lightBlueBg'),
        maxWidth: responsive('auto', '375px'),
        mx: 'auto',
        py: responsive('1em', height > 630 ? 0 : '1em'),
        position: responsive('static', height > 630 ? 'absolute' : 'static'),
        left: '50%',
        transform: responsive('none', height > 630 ? 'translateX(-50%)' : 'none'),
        bottom: '2em',
      }}
      logoWidth="3.25em"
      noPt
      headerColor="white"
      height={responsive('100%', height > 811 ? '812px' : '100%')}
      measureHeight={height}
    >
      <Box bg="custom.lightBlueBg" height={responsive(height > 650 ? `calc(100% - 57.25px)` : '100%', '100%')} maxWidth={responsive('auto', '375px')} m="auto">
        <Banner width={834} height={717} images={mobileImages} />
        {/* <Media greaterThanOrEqual="tablet">
          <Banner width={2560} height={1140} images={desktopImages} />
        </Media> */}
        <Box.Relative zIndex={5}>
          <Container textAlign="center">
            <Text
              fontSize="1em"
              fontWeight="bold"
              color="custom.darkBlue"
              textAlign="center"
              lineHeight="1.75"
            >
              悶了好久，超想出國玩一波！<br />
              憑直覺決定 6 件生活小事，<br />
              開門抵達和你最配的旅行地！
            </Text>
            <Button.Yellow
              as={p => <PaintLink hex={teal} {...p} />}
              my="1.375rem"
              to="q/1"
              maxWidth="180px"
              height="50px"
              fontSize="1.125rem"
            >
              進入 OMG 任意門
            </Button.Yellow>
            <Text
              fontSize="0.875em"
              color="custom.textGray"
              textAlign="center"
            >
              完成測驗領取驚喜彩蛋，<br />
              9.10 前騎乘 GoShare，<br />
              憑發票就抽歐美十大度假勝地雙人來回機票！
            </Text>
          </Container>
        </Box.Relative>
      </Box>
    </Layout>
  )
}

export default IndexPage
