import { graphql } from 'gatsby'
import Page from 'containers/HomePage'

export default Page

export const query = graphql`
query IndexQuery {
  allMobileJson {
    nodes {
      name
      width
      height
      x
      y
      depth
      image {
        childImageSharp {
          gatsbyImageData(
            layout: FULL_WIDTH
          )
        }
      }
    }
  }
}
`
